<template>
  <div class="container">
    <h2 class=""><i18n path="common.install"></i18n></h2>
    <hr class="my-2">
    <p><i18n path="common.guide.useNpm"></i18n></p>
    <showcode :trigger="false">
      <pre class="m-0">npm i -S v-page</pre>
    </showcode>

    <p><i18n path="common.guide.include"></i18n></p>
    <showcode :trigger="false">
      <v-code lang="javascript">
        <pre>
        // plugin install for global deploy
        import Vue from 'vue'
        import page from 'v-page';
        Vue.use(page, { global config options... });

        // use v-page in local component
        import { vPage } from 'v-page';
        export default {
          components: {
            'v-page': vPage
          }
        }
        </pre>
      </v-code>
    </showcode>

    <div class="card p-4 text-monospace">
      <p><i18n path="common.globalConfig"></i18n></p>
      <ul>
        <li>info</li>
        <li>pageSizeMenu</li>
        <li>language</li>
        <li>align</li>
        <li>border</li>
        <li>pageNumber</li>
        <li>first</li>
        <li>last</li>
      </ul>
    </div>

    <h2 class="mt-5"><i18n path="common.deploy"></i18n></h2>
    <hr class="my-2">
    <showcode :trigger="false">
      <v-code>
        <pre>
        &lt;template&gt;
          &lt;v-page :total-row="totalRow" @page-change="pageChange"&gt;&lt;/v-page&gt;
        &lt;/template&gt;

        &lt;script&gt;
        export default {
          data(){
            return {
              totalRow: 100//required option
            }
          },
          methods:{
            //receive page info change callback
            pageChange(pInfo){
              console.log(pInfo);//{pageNumber: 1, pageSize: 10}
              //for example, do some http request, get new data and update totalRow property
              axios({
              ...
              }).then(resp => {
                this.totalRow = resp.totalRow;
              });
            }
          }
        }
        &lt;/script&gt;
        </pre>
      </v-code>
    </showcode>

    <h2 class="mt-5"><i18n path="common.example"></i18n></h2>
    <hr class="my-2">
    <v-page class="mt-4" :total-row="21" align="center" @page-change="pageChange"></v-page>
    <div class="text-center mt-2 text-monospace">
      <h3 class="my-4"><i18n path="page.demo.pageChangeLogs"></i18n></h3>
      <div v-for="(log, index) in pageMsg" :key="index">{{log}}</div>
    </div>

    <showcode>
      <v-code>
        <pre>
        &lt;v-page :total-row="21" align="center" @page-change="pageChange"&gt;
        &lt;/v-page&gt;
        </pre>
      </v-code>
    </showcode>

    <h4 class="mt-5">
      <i18n path="page.demo.gallery"></i18n>
      <small class="ml-3">
        <a
          href="https://codepen.io/terry05/pen/yjZYLR"
          target="_blank"
        >
          <i18n path="common.codePen"></i18n>
        </a>
      </small>
    </h4>
    <div class="photo-wall">
      <div v-for="(num, index) in pageArr" :key="index" v-html="num" class="brick"></div>
    </div>
    <v-page :total-row="arr.length" align="left" @page-change="pagePhotoChange"></v-page>

    <showcode>
      <v-code>
        <pre>
        &lt;div class="photo-wall"&gt;
          &lt;div v-for="num in pageArr" v-html="num" class="brick"&gt;&lt;/div&gt;
        &lt;/div&gt;
        &lt;v-page :total-row="arr.length" align="left" @page-change="pagePhotoChange"&gt;
        &lt;/v-page&gt;

        &lt;script&gt;
        export default {
          data(){
            return {
              arr: Array(108).fill(0).map((val, index) => index + 1),
              pageArr: []
            }
          },
          methods: {
            pagePhotoChange(pInfo){
              let start=0, end=0;
              start = pInfo.pageSize * (pInfo.pageNumber-1);
              end = start + pInfo.pageSize;
              if(end > this.arr.length) end = this.arr.length;

              this.pageArr.splice(0, this.pageArr.length);
              for (let i = start; i &lt; end; i++) {
                this.pageArr.push(this.arr[i]);
              }
            }
          }
        }
        &lt;/script&gt;
        </pre>
      </v-code>
    </showcode>

    <h4 class="mt-5"><i18n path="page.demo.complete"></i18n></h4>
    <v-page align="left" :total-row="100" v-model="current" ref="page"></v-page>

    <div class="row my-2">
      <div class="col-md-1">
        <input type="text" class="form-control" v-model="target">
      </div>
      <button class="btn btn-secondary mr-3" type="button" @click="go">forward</button>
      <button
        class="btn btn-outline-secondary"
        type="button"
        @click="current = current + 1"
      >page + 1 (v-model)</button>
    </div>
    <showcode>
      <v-code>
        <pre>
        &lt;v-page align="left" :total-row="100" v-model="current" ref="page"&gt;&lt;/v-page&gt;

        &lt;button class="btn btn-secondary" type="button" @click="go"&gt;forward&lt;/button&gt;
        &lt;button
          class="btn btn-outline-secondary"
          type="button"
          @click="current = current + 1"
        &gt;page + 1&lt;/button&gt;

        &lt;script&gt;
        export default {
          data(){
            return {
              current: 3
            }
          },
          methods: {
            go(){
              //use api to change current page
              this.$refs.page.goPage(3);
            }
          }
        }
        &lt;/script&gt;
        </pre>
      </v-code>
    </showcode>

    <h4 class="mt-5"><i18n path="page.demo.noLength"></i18n></h4>
    <v-page :total-row="100" align="left" :page-size-menu="false"></v-page>
    <showcode>
      <v-code>
        <pre>
        &lt;v-page :total-row="100" align="left" :page-size-menu="false"&gt;
        &lt;/v-page&gt;
        </pre>
      </v-code>
    </showcode>

    <h4 class="mt-5"><i18n path="page.demo.noPageInfo"></i18n></h4>
    <v-page :total-row="100" align="left" :page-size-menu="false" :info="false"></v-page>
    <showcode>
      <v-code>
        <pre>
        &lt;v-page :total-row="100" align="left" :page-size-menu="false" :info="false"&gt;
        &lt;/v-page&gt;
        </pre>
      </v-code>
    </showcode>

    <h4 class="mt-5"><i18n path="page.demo.noFirstLast"></i18n></h4>
    <v-page :page-size-menu="false" :info="false" :total-row="100" :first="false" :last="false" align="left"></v-page>
    <showcode>
      <v-code>
        <pre>
        &lt;v-page
          :page-size-menu="false"
          :info="false"
          :total-row="100"
          :first="false"
          :last="false"
          align="left"
        &gt;&lt;/v-page&gt;
        </pre>
      </v-code>
    </showcode>

    <h4 class="mt-5"><i18n path="page.demo.noPageNumber"></i18n></h4>
    <v-page :page-size-menu="false"
            :info="false"
            :total-row="100"
            :first="false"
            :last="false"
            :page-number="false"
            align="left"></v-page>
    <showcode>
      <v-code>
        <pre>
        &lt;v-page
          :page-size-menu="false"
          :info="false"
          :total-row="100"
          :first="false"
          :last="false"
          :page-number="false"
          align="left"
        &gt;&lt;/v-page&gt;
        </pre>
      </v-code>
    </showcode>

    <h4 class="mt-5"><i18n path="page.demo.noBorder"></i18n></h4>
    <v-page :total-row="100" align="left" :border="false" ></v-page>
    <showcode>
      <v-code>
        <pre>
        &lt;v-page :total-row="100"  align="left" :border="false"&gt;
        &lt;/v-page&gt;
        </pre>
      </v-code>
    </showcode>

    <h4 class="mt-5"><i18n path="page.option.disabled"></i18n></h4>
    <v-page :total-row="100" align="left" :disabled="disabled" ></v-page>

    <div class="btn-group mt-2" role="group" aria-label="...">
      <button type="button" class="btn btn-outline-dark" :disabled="!disabled" @click="disabled=false">Enabled</button>
      <button type="button" class="btn btn-danger" :disabled="disabled" @click="disabled=true">Disabled</button>
    </div>
    <showcode>
      <v-code>
        <pre>
        &lt;v-page :total-row="100" align="left" :disabled="disabled"&gt;
        &lt;/v-page&gt;

        &lt;div class="btn-group"&gt;
          &lt;button :disabled="!disabled" @click="disabled=false"&gt;Enabled&lt;/button&gt;
          &lt;button :disabled="disabled" @click="disabled=true"&gt;Disabled&lt;/button&gt;
        &lt;/div&gt;
        </pre>
      </v-code>
    </showcode>

    <h2 class="mt-5"><i18n path="common.options"></i18n></h2>
    <table class="table table-striped option-table">
      <thead>
        <tr>
          <th><i18n path="common.tbTitle.name"></i18n></th>
          <th><i18n path="common.tbTitle.type"></i18n></th>
          <th><i18n path="common.tbTitle.desc"></i18n></th>
          <th><i18n path="common.tbTitle.required"></i18n></th>
          <th><i18n path="common.tbTitle.default"></i18n></th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td>v-model</td>
          <td>number</td>
          <td><i18n path="page.option.vModel"></i18n></td>
          <td>true</td>
          <td>0</td>
        </tr>
        <tr>
          <td>total-row</td>
          <td>number</td>
          <td><i18n path="page.option.totalRow"></i18n></td>
          <td>true</td>
          <td>0</td>
        </tr>
        <tr>
          <td>info</td>
          <td>boolean</td>
          <td><i18n path="page.option.info"></i18n></td>
          <td>false</td>
          <td>true</td>
        </tr>
        <tr>
          <td>language</td>
          <td>string</td>
          <td>
            <i18n path="page.option.language"></i18n>
            <ul class="mt-3">
              <li><code>'cn'</code></li>
              <li><code>'en'</code></li>
              <li><code>'jp'</code></li>
            </ul>
          </td>
          <td>false</td>
          <td>'cn'</td>
        </tr>
        <tr>
          <td>page-size-menu</td>
          <td>array|boolean</td>
          <td><i18n path="page.option.pageSizeMenu"></i18n></td>
          <td>false</td>
          <td>[10, 20, 50, 100]</td>
        </tr>
        <tr>
          <td>align</td>
          <td>string</td>
          <td>
            <i18n path="page.option.align"></i18n>
            <ul class="mt-3">
              <li><code>'left'</code></li>
              <li><code>'center'</code></li>
              <li><code>'right'</code></li>
            </ul>
          </td>
          <td>false</td>
          <td>'right'</td>
        </tr>
        <tr>
          <td>disabled</td>
          <td>boolean</td>
          <td>
            <i18n path="page.option.disabled"></i18n>
          </td>
          <td>false</td>
          <td>false</td>
        </tr>
        <tr>
          <td>border</td>
          <td>boolean</td>
          <td>
            <i18n path="page.option.border"></i18n>
          </td>
          <td>false</td>
          <td>true</td>
        </tr>
        <tr>
          <td>page-number</td>
          <td>boolean</td>
          <td>
            <i18n path="page.option.pageNumber"></i18n>
          </td>
          <td>false</td>
          <td>true</td>
        </tr>
        <tr>
          <td>first</td>
          <td>boolean</td>
          <td>
            <i18n path="page.option.first"></i18n>
          </td>
          <td>false</td>
          <td>true</td>
        </tr>
        <tr>
          <td>last</td>
          <td>boolean</td>
          <td>
            <i18n path="page.option.last"></i18n>
          </td>
          <td>false</td>
          <td>true</td>
        </tr>
      </tbody>
    </table>

    <h2 class="mt-5"><i18n path="common.events"></i18n></h2>
    <table class="table table-striped">
      <thead>
      <tr>
        <th><i18n path="common.tbTitle.param"></i18n></th>
        <th><i18n path="common.tbTitle.desc"></i18n></th>
        <th><i18n path="common.tbTitle.args"></i18n></th>
      </tr>
      </thead>
      <tbody>
      <tr>
        <td>page-change</td>
        <td><i18n path="page.event.pChange"></i18n></td>
        <td>
          <ul>
            <li><code>pageNumber</code><br><i18n path="page.event.pageNumber"></i18n></li>
            <li><code>pageSize</code><br><i18n path="page.event.pageSize"></i18n></li>
          </ul>
        </td>
      </tr>
      </tbody>
    </table>

    <h2 class="page-header">API</h2>
    <hr class="my-2">
    <p class=""><i18n path="selectmenu.api.desc"></i18n></p>

    <showcode :trigger="false">
      <v-code>
        <pre>
        &lt;v-page :total-row="100" ref="page" &gt;
        &lt;/page&gt;

        &lt;script&gt;
        //call api
        this.$refs.page.goPage(3);
        &lt;/script&gt;
        </pre>
      </v-code>
    </showcode>

    <div class="api-row">
      <h4>goPage<small><i18n path="page.api.goPage"></i18n></small></h4>
      <p>
        (page: number): void
      </p>
    </div>
    <div class="api-row">
      <h4>reload<small><i18n path="page.api.reload"></i18n></small></h4>
      <p>
        (): void
      </p>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      arr: Array.apply(null, { length: 108 }).map((val, index) => index + 1),
      pageArr: [],
      pageMsg: [],
      disabled: false,
      target: 1,
      current: 3
    }
  },
  methods: {
    pageChange (pInfo) {
      this.pageMsg.push(pInfo)
    },
    pagePhotoChange (pInfo) {
      // console.log(pInfo);
      this.pageArr = []
      let start = 0; let end = 0
      start = pInfo.pageSize * (pInfo.pageNumber - 1)
      end = start + pInfo.pageSize - 1
      this.pageArr = this.arr.filter((val, idx) => idx >= start && idx <= end)
    },
    go () {
      this.$refs.page.goPage(Number(this.target))
    }
  }
}
</script>

<style lang="scss">
div.photo-wall {
  div.brick {
    border-radius: 2px;
    background-color: #F4F4F4;
    text-align: center;
    line-height: 100%;
    float: left;
    margin: 0 10px 10px 0;
    width: 180px;
    height: 130px;
    font-size: 60px;
    color: #BBBBBB;
    padding-top: 35px;
  }
}
</style>
